@import url(https://fonts.googleapis.com/css?family=Archivo+Black);
@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500);
* {
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
  font-weight: 400; }

.app {
  display: grid;
  grid-template-columns: minmax(200px, 1fr) 4fr;
  padding-top: 80px; }

.size-filter {
  grid-column: 1 / 2;
  padding: 0 10px;
  width: 70%;
  position: relative;
  left: 30%; }
  .size-filter-title {
    margin-bottom: 10px; }
  .size-filter-icon-container {
    display: flex;
    flex-wrap: wrap; }
    .size-filter-icon-container .size-filter-icon {
      display: flex;
      width: 30px;
      height: 30px;
      background-color: gainsboro;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      margin-right: 10px;
      margin-bottom: 10px;
      cursor: pointer;
      font-size: 10px; }
      .size-filter-icon-container .size-filter-icon:hover {
        border: 1px solid black; }

.shelf {
  grid-column: 2 / 3;
  width: 90%; }
  .shelf .order-filter {
    margin-bottom: 30px; }
    .shelf .order-filter-select {
      float: right; }
  .shelf .product-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); }
    .shelf .product-list .product {
      padding: 10px;
      margin-bottom: 20px;
      text-align: center; }
      .shelf .product-list .product:hover {
        outline: 1px solid gainsboro; }
      .shelf .product-list .product-thumb {
        display: inline-block;
        width: 100%; }
      .shelf .product-list .product-name {
        height: 50px; }
      .shelf .product-list .product-price {
        height: 25px; }
      .shelf .product-list .product-add-to-cart {
        background-color: black;
        height: 30px;
        width: 100%;
        color: #e5eaed;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer; }
        .shelf .product-list .product-add-to-cart:hover {
          background-color: #336393; }

.cart {
  position: fixed;
  top: 0px;
  width: 500px;
  height: 100vh;
  background-color: #282828;
  color: white;
  -webkit-transition: right 0.2s;
  transition: right 0.2s; }
  .cart-button {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 50px;
    width: 50px;
    background-color: #121212;
    right: 50px;
    cursor: pointer; }
    .cart-button:hover {
      background-color: #336393; }
  .cart-container {
    display: grid;
    height: 100vh;
    grid-template-rows: 100px 1fr 200px; }
    .cart-container .cart-title {
      text-align: center;
      margin-bottom: 50px;
      grid-row: 1 / 2; }
    .cart-container .cart-shelf {
      grid-row: 2 / 3;
      overflow: scroll; }
      .cart-container .cart-shelf .nothing-in-cart {
        text-align: center; }
      .cart-container .cart-shelf .cart-product {
        height: 80px;
        width: 80%;
        position: relative;
        left: 10%;
        display: grid;
        grid-template-columns: 1fr 3fr 1fr 10px;
        margin-bottom: 20px; }
        .cart-container .cart-shelf .cart-product-thumb {
          grid-column: 1 / 2; }
        .cart-container .cart-shelf .cart-product-information {
          grid-column: 2 / 3; }
        .cart-container .cart-shelf .cart-product-quantity {
          grid-column: 3 / 4; }
        .cart-container .cart-shelf .cart-product-remove {
          grid-column: 4 / 5; }
          .cart-container .cart-shelf .cart-product-remove-icon {
            cursor: pointer; }
    .cart-container .checkout {
      grid-row: 3 / 4;
      background-color: #1a1a1a; }
      .cart-container .checkout-subtotal {
        width: 100%;
        text-align: center;
        margin-top: 25px; }
        .cart-container .checkout-subtotal-number {
          margin-left: 20px; }
      .cart-container .checkout-button {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: black;
        height: 50px;
        width: 80%;
        position: relative;
        left: 10%;
        top: 25px; }
        .cart-container .checkout-button:hover {
          background-color: #336393; }

@media only screen and (max-width: 600px) {
  .order-filter > span {
    display: block; }
  .order-filter-select {
    float: left !important; } }

